import { Component, OnInit } from "@angular/core";
import { ActivatedRoute, Router } from "@angular/router";
import { TemplatesDataService } from "../../services/templates-data.service";
import { SEOService } from "../../services/seo.service";
@Component({
  selector: "app-templates",
  templateUrl: "./templates.component.html",
  styleUrls: ["./templates.component.scss"]
})
export class TemplatesComponent implements OnInit {
  // template_id;
  templates;
  dataFound: boolean = true;
  loading: boolean = true;
  constructor(
    public activeRoute: ActivatedRoute,
    public templateData: TemplatesDataService,
    public routerR: Router,
    public _seoService: SEOService
  ) {}
  ngOnInit() {
    this.activeRoute.params.subscribe(routeParams => {
      this.loading = true;
      this.templateData.getTemplateByCategory(routeParams.cat_slug).subscribe(
        res => {
          if (res[0] == "No data Found") {
            this.dataFound = false;
          } else {
            this.dataFound = true;
            this._seoService.updateDescription(
              res["meta_data"].meta_description
            );
            // this._seoService.updateKeywords(res["meta_data"].meta_keyword);
            this.templates = res["template_data"];
          }
          this.loading = false;
        },
        err => {
          console.log(err);
          this.loading = false;
        }
      );
    });
  }
}
