import { Injectable } from "@angular/core";
import { Resolve, ActivatedRouteSnapshot } from "@angular/router";
import { Observable } from "rxjs";
import { TemplatesDataService } from "./templates-data.service";

@Injectable({
  providedIn: "root"
})
export class AppresolverService implements Resolve<any> {
  constructor(public templateDataServ: TemplatesDataService) {}
  resolve(route: ActivatedRouteSnapshot): Observable<any> {
    return this.templateDataServ.getSingleTemplate(
      route.paramMap.get("template_slug")
    );
  }
}
