import { Component, OnInit, OnDestroy } from "@angular/core";
import { TemplatesDataService } from "../../services/templates-data.service";
import {menuList,CategoriesList,category_data} from '../../Model/Categories'
import { Subscription } from 'rxjs';


@Component({
  selector: "app-navbar",
  templateUrl: "./navbar.component.html",
  styleUrls: ["./navbar.component.scss"]
})
export class NavbarComponent implements OnInit, OnDestroy {

  public cateMenuList: menuList[] = [];
  public showDropdown: boolean = false;

  private $getAllCategories: Subscription;

  constructor(public templateData: TemplatesDataService) { }

  ngOnInit() {
    this.CategoryMenuBind();
  }

  CategoryMenuBind() {

    const catColumnCnt = 3;

    this.$getAllCategories = this.templateData.getAllCategories().subscribe(
      (catList: CategoriesList) => {
        console.log("catList", catList);
        if (catList.category_data && catList.category_data.length > 0) {
          this.showDropdown = true;
          let catDat: category_data[] = catList.category_data;
          let loopCnt = Math.ceil(catDat.length / catColumnCnt);

          for (let i = 1; i < loopCnt; i++) {
            this.cateMenuList.push({
              id: i,
              catList: this.paginateArray(catDat, loopCnt, i)
            });
          }
          console.log("cateMenuList", this.cateMenuList);
        }
      },
      err => {
        console.log(err);
      }
    );
  }

  paginateArray = (array, page_size, page_number) => {
    --page_number; // because pages logically start with 1, but technically with 0
    return array.slice(page_number * page_size, (page_number + 1) * page_size);
  }

  ngOnDestroy() {
    if (this.$getAllCategories) {
      this.$getAllCategories.unsubscribe();
    }
  }


}
