import { Injectable } from "@angular/core";
import { HttpClient } from "@angular/common/http";
@Injectable({
  providedIn: "root"
})
export class TemplatesDataService {
  default_templates;
  constructor(public http: HttpClient) {}
  getAllCategories() {
    return this.http.get(
      "https://www.trycatchclasses.com/free-templates/backbone/api/all_categories"
    );
  }
  getDefaultTemplates() {
    return this.http.get(
      "https://www.trycatchclasses.com/free-templates/backbone/api/random_templates"
    );
  }
  getTemplateByCategory(cat_slug) {
    return this.http.get(
      "https://www.trycatchclasses.com/free-templates/backbone/api/get_templates/" +
        cat_slug
    );
  }
  getSingleTemplate(template_slug) {
    return this.http.get(
      "https://www.trycatchclasses.com/free-templates/backbone/api/get_single_template/" +
        template_slug
    );
  }
  getRelatedTemplates(cat_slug, template_id) {
    return this.http.get(
      "https://www.trycatchclasses.com/free-templates/backbone/api/get_related_template/" +
        cat_slug +
        "/" +
        template_id
    );
  }
}