import { SEOService } from "./../../services/seo.service";
import { Component, OnInit } from "@angular/core";
import { TemplatesDataService } from "../../services/templates-data.service";
import { Meta } from "@angular/platform-browser";
import { Router } from "@angular/router";

@Component({
  selector: "app-home",
  templateUrl: "./home.component.html",
  styleUrls: ["./home.component.scss"]
})
export class HomeComponent implements OnInit {
  loading: boolean = true;
  constructor(
    public templateData: TemplatesDataService,
    public meta: Meta,
    public router: Router,
    public _seoService: SEOService
  ) {}
  default_templates;
  ngOnInit() {
    this.templateData.getDefaultTemplates().subscribe(
      res => {
        this.loading = true;
        this.default_templates = res["template_detail"];
        this._seoService.updateDescription(res["meta_data"].meta_description);
        //this._seoService.updateKeywords(res["meta_data"].meta_keyword);
        this.loading = false;
      },
      err => {
        console.log(err);
        this.loading = false;
      }
    );
  }
}
