import { AppresolverService } from "./services/appresolver.service";
import { PageNotFoundComponent } from "./components/page-not-found/page-not-found.component";
import { SingleTemplateComponent } from "./components/single-template/single-template.component";
import { HomeComponent } from "./components/home/home.component";
import { NgModule } from "@angular/core";
import { Routes, RouterModule } from "@angular/router";

import { MDBBootstrapModule } from "angular-bootstrap-md";
import { TemplatesComponent } from "./components/templates/templates.component";
import { CommonModule } from "@angular/common";

const routes: Routes = [
  { path: "", component: HomeComponent },
  { path: "home", component: HomeComponent },
  { path: ":cat_slug", component: TemplatesComponent },
  {
    path: ":cat_slug/:template_slug",
    component: SingleTemplateComponent,
    resolve: { cres: AppresolverService }
  },
  { path: "**", component: PageNotFoundComponent }
];

@NgModule({
  declarations: [
    TemplatesComponent,
    HomeComponent,
    SingleTemplateComponent,
    PageNotFoundComponent
  ],
  imports: [
    RouterModule.forRoot(routes),
    MDBBootstrapModule.forRoot(),
    CommonModule
  ],
  exports: [RouterModule, MDBBootstrapModule],
  providers: [AppresolverService]
})
export class AppRoutingModule {}
